import { Controller } from '@ali/4ever-cangjie';

export default function isAboveCode(controller: Controller) {
  const { selection, document } = controller.value;
  if (!selection) {
    return false;
  }
  const { isCollapsed } = selection;
  const start = selection.getStart(document);
  const nextBlock = document.getNextBlock(start.key);
  if (!isCollapsed || !nextBlock) {
    return false;
  }
  return nextBlock.type === 'code';
}
